// Components
import BaseHeader from '@/components/argon-core/BaseHeader'
import MainTitle from '@/components/shack/MainTitle.vue'
import { Table, TableColumn } from 'element-ui'

import { mapState } from 'vuex'

export default {
  layout: 'ShackDash',
  components: {
    BaseHeader,
    MainTitle,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  async asyncData({ store, $tag, error }) {
    await $tag
      .getAllTags()
      .then(({ data }) => {
        store.commit('tags/setTags', data)
      })
      .catch(err => {
        const message = err.response
          ? JSON.stringify(err.response.data.errors)
          : err.message

        error({
          statusCode: err.status,
          message
        })
      })
  },
  data() {
    return {
      loading: false,
      name: '',
      type: ''
    }
  },
  computed: {
    ...mapState({
      tags: state => state.tags.tags.data,
      meta: state => state.tags.tags.meta,
      space: state => state.space.currentSpace
    })
  },
  methods: {
    next() {
      const { current_page, last_page } = this.meta
      const nextPage = last_page === current_page ? 1 : current_page + 1

      this.$tag
        .getAllTags(nextPage)
        .then(({ data }) => {
          this.$store.commit('tags/setTags', data)
        })
        .catch(err => {
          const message = err.response
            ? JSON.stringify(err.response.data.errors)
            : err.message

          this.$bvToast.toast(message, {
            title: 'Error',
            variant: 'danger',
            solid: true
          })
        })
    },
    prev() {
      const { current_page } = this.meta
      const prevPage = current_page === 1 ? 1 : current_page - 1

      this.$tag
        .getAllTags(prevPage)
        .then(({ data }) => {
          this.$store.commit('tags/setTags', data)
        })
        .catch(err => {
          const message = err.response
            ? JSON.stringify(err.response.data.errors)
            : err.message

          this.$bvToast.toast(message, {
            title: 'Error',
            variant: 'danger',
            solid: true
          })
        })
    },
    async removeTag(row) {
      const tagType = row.type
      const payload = {
        data: {
          name: row.name
        }
      }
      this.loading = !this.loading
      await this.$tag
        .removeTag(tagType, payload)
        .then(res => {
          this.$bvToast.toast('Tag removed successfully', {
            title: 'Success',
            variant: 'success',
            solid: true
          })
          this.$store.commit('tags/removeTag', row)
          this.loading = !this.loading
        })
        .catch(err => {
          const message = err.response
            ? JSON.stringify(err.response.data.errors)
            : err.message

          this.$bvToast.toast(message, {
            title: 'Error',
            variant: 'danger',
            solid: true
          })
          this.loading = !this.loading
        })
    },
    showEditModal(data) {
      this.$bvModal.show('edit-modal')
      this.name = data.name
      this.type = data.type
      this.old_tag = data.name
    },
    async editTag() {
      const { name, type, old_tag } = this

      const payload = {
        old_tag,
        new_tag: name
      }

      this.loading = !this.loading

      this.$tag
        .updateTag(type, payload)
        .then(({ data: { data } }) => {
          this.$bvToast.toast(`Tag ~${name}~ updated successfully`, {
            title: 'Success',
            variant: 'success',
            solid: true
          })

          this.$store.commit('tags/updateTag', { data, old_tag })
          this.loading = !this.loading
          this.$bvModal.hide('edit-modal')
        })
        .catch(err => {
          const message = err.response
            ? JSON.stringify(err.response.data.errors)
            : err.message

          this.$bvToast.toast(message, {
            title: 'Error',
            variant: 'danger',
            solid: true
          })
          this.loading = !this.loading
        })
    }
  }
}
